export async function getMarketHistory(input: GetMarketHistoryInput) {
    console.log('Fetching market history for:', input.marketId, 'days:', input.days);

    // Fetch real historical data from Polymarket
    const history = await fetchMarketHistory(input.marketId, input.days);

    return {
        marketId: input.marketId,
        chain: input.chain,
        history,
        trend: history.length > 1 && history[history.length - 1].yesPrice > history[0].yesPrice ? 'upward' : 'downward',
        volatility: calculateVolatility(history),
    };
}

function calculateVolatility(history: any[]): number {
    if (history.length < 2) return 0;

    const prices = history.map(h => h.yesPrice);
    const mean = prices.reduce((a, b) => a + b, 0) / prices.length;
    const variance = prices.reduce((sum, price) => sum + Math.pow(price - mean, 2), 0) / prices.length;

    return Math.sqrt(variance);
}
